import { Link, useHistory } from 'react-router-dom';
import React, {useEffect, useState} from 'react';
import DataList from '../data/workList.json';
import Fade from 'react-reveal/Fade';
import Scroll from '../comp/Scroll';
import Detail from './Detail';
import Header from '../comp/Header';
import Footer from '../comp/Footer';


export default function Work(){

    const workList = DataList.List.map((item,key)=>(
        <li key ={key} id={key}>
            <Link to={`/Detail/:${key}`}>
                <img src={item.bg} alt=""></img>

                <div className="con">
                    <div className="con_tit">{item.detial_sub}</div>
                    <p>{item.detail_tit}</p>
                    <div className="skill">
                        <span>{item.detail_skill}</span>
                        <span>{item.detail_skill}</span>
                    </div>
                </div>
            </Link>
        </li>
    ));  

    return(
        <>
        <Header/>

        <div className="work">
            <ul>
                {workList}
            </ul>
            
        </div>

        <Footer/>
        </>
    )
}