import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import '../App.css';
import Header from '../comp/Header';
import DataList from '../data/workList.json';
import Fade from 'react-reveal/Fade';

export default function Detail(match){

    let history = useHistory();

    const id = match.location.pathname.split(':');
    const num = id[1];

    const list = DataList.List

    const next = Number(num)+1;

    function nextPage(){
        if(next == list.length){
            window.location.href=`:0`;
            history.push(':0');
        }else{
            window.location.href=`:${next}`;
            history.push(`:${next}`);
        }
    }

    const width = ["1920","1536","640"]
    const height = ["1080","2048","1136"]

    function resize(url,width,height){
        window.open(url,"url",`width=${width}, height=${height} history=no, resizable=no, status=no, scrollbars=no, menubar=no`)
    }

    const responsive = DataList.List[num].respon.map((item,key)=>(
        <p className="about-resume" onClick={()=>{resize(DataList.List[num].site1, width[key], height[key])}} id={key}>{item}</p>
    ));

    return(
        <>
        <Header/>
        <div className="detail-bg-scroll" style={{background:"url("+list[num].bg2+") no-repeat center center", backgroundSize:"cover"}}>
            <div className="detail-tit">{list[num].detail_tit}</div>
            <div className="detail-tit">{list[num].detial_sub}</div>
        </div>

        <div className="detail">
            <div className="left">
                <h2>{list[num].detail_tit_kor}</h2>
                <small>{list[num].detail_tit_eng}</small>

                <ul>
                    <li>
                        <span>ì‘ì—…ë°©ì‹</span>
                        <span>ê¸°ì—¬ë„</span>
                        <span>ì‘ì—…ê¸°ê°„</span>
                        <span>ê°œë°œí™˜ê²½</span>
                        <span>ê°œë°œì–¸ì–´</span>
                    </li>

                    <li>
                        <span>{list[num].detail_work}</span>
                        <span>{list[num].detail_per}</span>
                        <span>{list[num].detail_date}</span>
                        <span>{list[num].detail_space}</span>
                        <span>{list[num].detail_skill}</span>
                    </li>
                </ul>
            </div>
            <div className="right">
                <div className="right_desc">
                    {list[num].detail_desc}
                </div>
                <div className="right_btn">
                    <a href={list[num].site1} target="_blank"><button className="about-resume">ì‚¬ì´íŠ¸ ë°”ë¡œê°€ê¸°</button></a>
                    <a href={list[num].site2} target="_blank"><button className="about-resume">ê¸°ì¡´ ì‚¬ì´íŠ¸ ë°”ë¡œê°€ê¸°</button></a>
                </div>
            </div>
        </div>

        <article className="row1">
            <video style={{marginRight:"5%"}} autoPlay muted loop playsInline>
                <source src={list[num].video1} type="video/mp4"></source>
            </video>

            <Fade bottom>
                <div className="txt">
                    <div className="txt_tit">{list[num].video1_tit}</div>
                    <span>{list[num].video1_desc}</span>
                </div>
            </Fade>
        </article>

        <article className="row1">
            <Fade bottom>
                <div className="txt">
                    <div className="txt_tit">{list[num].video2_tit}</div>
                    <span>{list[num].video2_desc}</span>
                </div>
            </Fade>

            <video style={{marginLeft:"5%"}} autoPlay muted loop playsInline>
                <source src={list[num].video2} type="video/mp4"></source>
            </video>
        </article>

        <article className="row2">
            <div>
                <video autoPlay muted loop playsInline>
                    <source src={list[num].video3} type="video/mp4"></source>
                </video>

                <video autoPlay muted loop playsInline>
                    <source src={list[num].video3_2} type="video/mp4"></source>
                </video>
            </div>
            
            <Fade bottom>
            <div className="txt">
                <div className="txt_tit">{list[num].video3_tit}</div>
                <span>{list[num].video3_desc}</span>
            </div>
            </Fade>
        </article>

        <article className="row3" style={{width:"100%"}}>
            <div className="fixBg" style={{background:"url("+list[num].mockup+") no-repeat center center", backgroundSize:"cover" , width:"100%", height:"80vh"}}>
            </div>
        </article>

        <article className="row4">
            <iframe title="Portfolio site" src={list[num].site1} class="portfolio-view"></iframe>
        </article>

        <div className="website">
                {responsive}
        </div>

        <div className="next" onClick="window.location.reload()">
            <Link to={next == list.length ? `/Detail/:0` : `/Detail/:${next}`}>
                <div className="next-fade">
                    <small>next page</small>
                    <span>
                        {next == list.length ? list[0].detial_sub : list[next].detial_sub} 
                    </span>
                </div>
                <div className="next-img">
                    <img src={next == list.length ? list[0].bg2 : list[next].bg2}></img>
                </div>
            </Link>
        </div>
        </>
    )
}